import uniNumberBox from '@/components/uni-number-box/uni-number-box.vue';
import uniSection from '@/components/uni-section/uni-section.vue';

function getDate(type) {
	const date = new Date();

	let year = date.getFullYear();
	let month = date.getMonth() + 1;
	let day = date.getDate();

	if (type === 'start') {
		// year = year - 60;
	} else if (type === 'end') {
		year = year + 2;
	}
	month = month > 9 ? month : '0' + month;
	day = day > 9 ? day : '0' + day;

	return `${year}-${month}-${day}`;
}
export default {
	components: {
		uniNumberBox,
		uniSection
	},
	data() {
		return {
			quxiaostate: 0,
			paystate: 0,
			intervalID: 0,
			havetime: '',
			id: '',
			type: 0,
			new_type: '',
			youhui: 0,
			maskState: 0, //优惠券面板显示状态
			desc: '', //备注
			payType: 1, //1微信 2支付宝
			numberValue: 1,
			minQuantity: 1,
			price: 0,
			all_price: 0,
			secondKillId: 0,
			secondKillTimeId: 0,
			guige_id: 0,
			miaosha: 0,
			buy_type: 0,
			couponSel: '',
			commodity: [],
			// selCouponName: '选择优惠券',
			total: 0,
			goods: [],
			service: [],
			note: '',
			date: getDate({
				format: true
			}),
			time: '2',
			startDate: getDate('start'),
			endDate: getDate('end'),
			couponList: [],
			addressData: {
				name: '添加地址',
				mobile: null
			}
		};
	},
	onUnload() {
		console.log('页面卸载')
		clearInterval(that.intervalID)
	},
	onLoad(option) {
		//商品数据
		//let data = JSON.parse(option.data);
		//console.log(data);
		var that = this;
		that.paystate = 0;
		that.quxiaostate = 0;
		let id = option.id;
		let type = option.type;
		let guige_id = option.guige_id;
		console.log(guige_id);
		that.id = option.id;
		that.type = option.type;
		that.guige_id = option.guige_id;
		that.buy_type = option.buy_type;
		that.loadData(id);
		if (type == 0) {
			that.new_type = 2;
		} else if (type == 1) {
			that.new_type = 0;
		} else if (type == 2) {
			that.new_type = 6;
		} else if (type == -1) {
			if (option.buy_type == 1) {
				that.new_type = 1;
			} else {
				that.new_type = 0;
			}

		} else if (type == -2) {
			if (option.buy_type == 1) {
				that.new_type = 7;
			} else {
				that.new_type = 6;
			}

		} else if (type == -3) {
			if (option.buy_type == 1) {
				that.new_type = 3;
			} else {
				that.new_type = 2;
			}
		}

		if (option.miaosha == 1) {
			that.miaosha = option.miaosha;
			that.secondKillId = option.secondKillId;
			that.secondKillTimeId = option.secondKillTimeId;
			console.log('is秒杀' + option.miaosha)
			that.new_type = 5;
		}
	},
	methods: {
		//取消订单
		cancelOrder() {
			let that = this;
			if (that.quxiaostate == 1) {
				return
			}
			that.quxiaostate = 1;
			uni.showLoading({
				title: '请稍后'
			});

			uni.request({
				url: that.$api_url + '/order/cancel', //仅为示例，并非真实接口地址。
				header: {
					'Content-Type': 'application/x-www-form-urlencoded',
					'authorization': uni.getStorageSync('authorization') //自定义请求头信息
				},
				method: 'POST',
				data: {
					orderNo: that.goods.number
				},
				success: res => {
					that.quxiaostate = 0;
					uni.navigateBack({
						delta: 2
					})
				},
				complete: function(e) {
					that.quxiaostate = 0;
					uni.hideLoading();
				}
			});
		},
		change(value) {
			console.log(value);
			this.numberValue = value;
			this.all_price = this.price * value;
			// this.total = ((this.goods.discountPrice * value)).toFixed(2);
			this.countTotal();
		},
		async loadData(id) {
			let that = this;
			this.loadGoodsInfo();

		},
		selAddress: function() {
			var that = this;
			uni.chooseAddress({
				success(res) {
					that.addressData = res;
					if (that.goods.pickUp) {
						that.addressData.provinceName = that.goods.pickUpAddress;
						that.addressData.cityName = '';
						that.addressData.detailInfo = '';
						uni.showModal({
							title: '提示',
							content: '该商品为自提，请注意自提地址！',
							showCancel: false,
							success: function(e) {

							}
						})
					}
					console.log(res.userName)
					console.log(res.postalCode)
					console.log(res.provinceName)
					console.log(res.cityName)
					console.log(res.countyName)
					console.log(res.detailInfo)
					console.log(res.nationalCode)
					console.log(res.telNumber)
				}
			})
		},
		nobonus: function() {
			this.selCouponName = '选择优惠券';
			this.couponSel = '';
			this.maskState = 0;
			this.youhui = 0;
			this.all_price = this.price * this.numberValue;
			this.countTotal();
		},

		loadGoodsInfo: function() {
			var that = this;
			console.log('this.miaosha==' + that.miaosha);
			var goodsinfo = 'order/detail';
			var vals = {
				orderId: that.id,
			};
			var xxx = this.$Request.topost(goodsinfo, vals);
			xxx.then(res => {
				// this.$Request.toget(this.$api.goods.goodsinfo, { productId: this.id, type: this.type }, { authorization: uni.getStorageSync('authorization') }).then(res => {
				console.log(res);
				if (res.code == 1000) {
					that.goods = res.content;

					var chooseTime = (new Date(this.goods.createTime)).getTime() + 24 * 60 * 60 * 1000;
					console.log(chooseTime);
					var nowTime = (new Date()).getTime();
					console.log(nowTime);
					this.havetime = Math.floor((chooseTime - nowTime) / 1000 / 3600) + '小时' + Math.floor((chooseTime - nowTime) /
						1000 % 3600 / 60);
					console.log(this.havetime);
					if (that.type != 0) {
						console.log(that.guige_id);
						that.commodity = res.content.commodityList[that.guige_id];
						that.minQuantity = res.content.commodityList[that.guige_id].minQuantity;
						if (that.buy_type == 1) {
							that.price = that.commodity.groupBuyPrice;
						} else {
							if (that.miaosha == 1) {
								that.price = that.commodity.secondKillPrice;
							} else {
								that.price = that.commodity.price;
							}
						}
					} else {
						that.minQuantity = 1;
						that.price = res.content.discountPrice;
					}

					that.countTotal();

					that.goods = res.content;
					that.$Request.topost(that.$api.order.bonus_list, {
						productId: that.id,
						price: that.price * that.minQuantity
					}).then(res => {
						if (res.code == 1000) {
							that.couponList = res.content;
						}
					});

					that.$Request.topost(that.$api.order.bonus_best, {
						productId: that.id,
						price: that.price * that.minQuantity
					}).then(res => {
						if (res.code == 1000) {
							that.couponSel = res.content.id;
							that.youhui = res.content.money;
							that.selCouponName = res.content.name;
							that.selCoupon(res.content);
							that.countTotal();
						}
					});

				} else {
					uni.navigateBack();
				}
			});




		},

		countTotal() {
			if (this.price * this.numberValue > this.youhui) {
				this.total = ((this.price * this.numberValue) - this.youhui).toFixed(2);
			} else {
				this.total = 0;
			}
		},


		//显示优惠券面板
		toggleMask(type) {
			let timer = type === 'show' ? 10 : 300;
			let state = type === 'show' ? 1 : 0;
			this.maskState = 2;
			setTimeout(() => {
				this.maskState = state;
			}, timer);
		},
		selCoupon(item) {
			this.youhui = item.money;
			this.all_price = this.price * this.numberValue;
			this.countTotal();
			this.selCouponName = item.name + "(￥" + item.money + "元)";
			this.couponSel = item.id;
			this.maskState = 0;
		},
		numberChange(data) {
			this.number = data.number;
		},
		changePayType(type) {
			this.payType = type;
		},

		submit() {
			let that = this;
			if (that.paystate == 1) {
				return
			}
			that.paystate = 1;
			uni.request({
				url: this.$api_url + '/weixin/payment.do', //仅为示例，并非真实接口地址。
				method: 'POST',
				header: {
					'Content-Type': 'application/x-www-form-urlencoded',
					'authorization': uni.getStorageSync('authorization') //自定义请求头信息
				},
				data: {
					"orderId": that.goods.id
				},
				success: res => {
					console.log(res)
					// 仅作为示例，非真实参数信息。
					uni.requestPayment({
						provider: 'wxpay',
						timeStamp: res.data.timeStamp,
						nonceStr: res.data.nonceStr,
						package: res.data.package,
						signType: 'MD5',
						paySign: res.data.paySign,
						success: function(res) {
							console.log('success:' + JSON.stringify(res));
							that.paystate = 0;
							uni.navigateBack({
								delta: 2
							})
						},
						fail: function(err) {
							console.log('fail:' + JSON.stringify(err));
							that.paystate = 0;
							uni.showModal({
								content: '确定要放弃本次支付吗？',
								success: function(res) {
									if (res.confirm) {
										uni.navigateBack({
											delta: 2
										})
									} else if (res.cancel) {
										console.log('用户点击取消');
									}
								}
							})

						}
					});
				},
				complete: function(e) {
				}
			});
		},

		bindDateChange: function(e) {
			this.date = e.target.value;
		},
		radioChange: function(e) {
			var checked = e.target.value
			this.time = checked;
			console.log(checked)
		},
		stopPrevent() {}
	}
};
